import document from 'global/document';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

// import images

import CategoryIcons from '../CategoryIcon';
// import InsurerLogoCarousel from './InsurerLogoCarousel';

// import css

// Time to delay animation
const delayTime = 1000;

class LandingInsuranceSection extends Component {
  constructor(props) {
    super(props);
    this.addAnimateInsuranceHandler = this.addAnimateInsuranceHandler.bind(this);
  }
  addAnimateInsuranceHandler() {
    const addAniDesc = document.getElementsByName('insurance-desc');
    const addAniImg = document.getElementsByName('insurance-img');
    // first animation starts when scroll
    for (let i = 0; i < addAniDesc.length; i++) {
      addAniDesc[i].classList.add('desc-ani');
    }
    // delay for second animation
    setTimeout(() => {
      for (let i = 0; i < addAniImg.length; i++) {
        addAniImg[i].classList.remove('hidden');
        addAniImg[i].classList.add('img-ani');
      }
    }, delayTime);
  }
  render() {
    console.log('RENDER Insurance section');
    return (
      <Col className="section-container-fixed  insurance-container d-block d-lg-none" >
        <Row className="insurance-inner-container" noGutters>
          <Col className="left-container des" xs={12}>
            <Row className="left-inner-container" noGutters>
              <Col>
                <h1>Insurances</h1>
                <p name="insurance-desc">Go ahead, choose a plan - there are plenty of options.
                  We have plans from different insurance partners
                  that work just for you.
                </p>
              </Col>
            </Row>
          </Col>
          <Col className="landing-category-section-small" xs={12} name="insurance-img">
            <CategoryIcons />
          </Col>
        </Row>
      </Col>
    );
  }
}

export default LandingInsuranceSection;
